.categories {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #f4ac04;
}

.categories li {
  cursor: pointer;
  margin: 10px;
  padding: 10px 20px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  background-color: #ffcc00;
  border-radius: 25px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

.categories li:hover {
  transform: scale(1.1);
  background-color: #f39c12;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4);
}

.categories li.active {
  background-color: #fff;
  color: #f7b731;
}

@media (max-width: 768px) {
  .categories {
    flex-direction: column;
    align-items: center;
  }

  .categories li {
    margin: 5px;
    padding: 10px;
    font-size: 1rem;
    box-shadow: none;
  }
}
