.container {
  min-height: 800px;
  margin: 0 auto;
}

.cart-page-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-width: 100%;
  min-height: 700px;
  background: #333;
  border-radius: 5px;
  padding: 2rem 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.carts h1 {
  font-size: 2rem;
  color: #ffcc00;
  margin-bottom: 1rem;
}

.carts-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.total-amount {
  background: #333;
  width: 100%;
  padding: 1rem;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: 1rem;
}

.order-details h1 {
  font-size: 1.3rem;
  color: #ffcc00;
  margin-bottom: 1rem;
}

.sum-of-products {
  font-size: 1.1rem;
  margin-right: 1rem;
}

.all-calculated {
  font-size: 1.3rem;
  font-weight: bold;
}

.discountBtn {
  background: #ffcc00;
  color: #fff;
  border: none;
  cursor: pointer;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border-radius: 5px;
  margin-right: 1rem;
}

.approve-btn {
  background: #ffcc00;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
}

.cart-icon {
  font-size: 3rem;
  color: #ffd700;
}

.cartDiv h1 {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 1rem;
}

.cartLink .start-btn {
  background: #ffd700;
  color: #333;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
}

@media (max-width: 600px) {
  .carts-container {
    grid-template-columns: 1fr;
  }
  .discountBtn {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}
