.count {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h2 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

p {
  font-size: 2rem;
  margin: 0;
}

.countDown {
  background: #ffcc00;
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transform: scale(0);
  animation: countdown 10s infinite;
}

@keyframes countdown {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
