.no-fav-container {
  background: #333;
  width: 100%;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
}

.heart {
  color: #ff0;
  font-size: 5rem;
  animation: spin 2s linear infinite;
}

.empty {
  color: #333;
  font-size: 2rem;
}

.emptyDesc {
  color: #666;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.start-btn {
  background: #333;
  color: #fff;
  font-size: 1.5rem;
  padding: 1rem 2rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.start-btn:hover {
  background: #ffcc00;
  border-radius: 12px;
  transform: scale(1.1);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 700px) {
  .no-fav-container {
    border-radius: 0;
  }

  .emptyDesc {
    font-size: 1.2rem;
  }
}
