body {
  background: #333;
}

.auth-page-container {
  max-width: 500px;
  margin: 2rem auto;
  padding: 2rem;
  border-radius: 0.5rem;
  background: #fff;
  text-align: center;
}

.texts h1 {
  font-size: 2rem;
  color: #ffc107;
}

.texts p {
  color: #888;
}

.buttons {
  margin: 1rem 0;
}

.auth-btn {
  padding: 0.5rem 1rem;
  border: 1px solid #ddd;
  border-radius: 0.3rem;
  font-size: 1.1rem;
  background: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.auth-btn:hover {
  background: #ffc107;
  color: #fff;
}

.selected {
  background: #ffc107;
  color: #fff;
}

.form-container {
  padding: 1rem;
  border-top: 1px solid #ddd;
}

@media (min-width: 700px) {
  .auth-page-container {
    padding: 4rem;
  }
}
