body {
  background: #333;
}

.module {
  max-width: 500px;
  margin: 2rem auto;
  padding: 2rem;
  border-radius: 0.5rem;
  background: #fff;
  text-align: center;
  color: #000;
}

.form-item {
  display: flex;
  flex-direction: column;
}

.input-box {
  position: relative;
  margin-bottom: 1rem;
}

.input-box input {
  padding: 0.5rem;
  border: 2px solid #ffc107;
  border-radius: 0.3rem;
}

.forgot-password {
  margin: 1rem 0;
  color: #ffc107;
}

.submit-btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.3rem;
  background: #ffc107;
  color: #fff;
  cursor: pointer;
}

.social-buttons {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}

.social-btn {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
  padding: 0.5rem 1rem;
  border: 2px solid #ffc107;
  border-radius: 0.5rem;
}

.social-text {
  margin-left: 1rem;
}

.icon {
  width: 2rem;
  height: 2rem;
}

.social-text span {
  display: block;
}

.brand {
  color: #ffc107;
  font-weight: bold;
}

.login-text {
  color: #000;
}

@media (min-width: 600px) {
  .social-buttons {
    flex-direction: row;
  }
}
