@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap");

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  color: #333;
  overflow-x: hidden;
}

.wrapper {
  background: #333;
  height: 90%;
  max-width: 1000%;

  margin: 0 auto;

  overflow: hidden;
}

.categories-container {
  background: #333;
  border-bottom: 1px solid #ccc;
  padding: 6rem 5rem;
}

.categories {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.category-item {
  text-align: center;
  transition: all 0.3s ease;
}

.category-item:hover {
  transform: scale(1.1);
}

.category-item h1 {
  font-size: 2rem;
  color: #fff;
}

.category-item-selected {
  background: #ffcc00;
  border-radius: 12px;
  padding: 1rem;
}

.category-item-selected h1 {
  color: #000;
}

.icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.productsContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 700px) {
  .categories {
    flex-direction: column;
  }
}

.category-item {
  margin-bottom: 1rem;
}

.main-content-container {
  background: #333;
  padding: 2rem;
}
Footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
