.cart-item-container {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
  border-bottom: 1px solid #eee;
}

.seller {
  color: #388e3c;
  font-size: 1.1rem;
}

.sellerDiv {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.checkbox {
  margin-right: 0.5rem;
}

.sellerText {
  margin-right: 0.5rem;
  color: #333;
}

.arrowIcon {
  color: #333;
}

.shipment {
  background: #333;
  color: #fff;
  padding: 0.3rem 0.5rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.product {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-details {
  display: flex;
  align-items: center;
}

.product-details img {
  width: 70px;
  height: 70px;
  margin-right: 1rem;
}

.product-name {
  cursor: pointer;
  font-size: 1.1rem;
}

.brand {
  color: #333;
  margin-right: 0.5rem;
}

.buttons {
  display: flex;
  align-items: center;
}

.counter {
  background: #fff;
  border: 1px solid #ccc;
  padding: 0.5rem;
  cursor: pointer;
  font-size: 1.3rem;
}

.amount {
  width: 2rem;
  text-align: center;
  border: 1px solid #333;
  padding: 0.5rem;
  font-size: 1.1rem;
}

.price-container {
  background: #333;
  padding: 0.5rem;
  border-radius: 5px;
  margin-right: 1rem;
}

.price {
  color: #ffcc00;
  font-size: 1.3rem;
}

.trash {
  cursor: pointer;
  color: red;
  font-size: 1.3rem;
}

@media (max-width: 600px) {
  .product-details img {
    width: 50px;
    height: 50px;
  }
  .product-name {
    font-size: 1rem;
  }
  .seller {
    font-size: 1rem;
  }
  .price {
    font-size: 1.2rem;
  }
  .counter {
    font-size: 1.1rem;
  }
}
