body {
  background: #333;
}

.module {
  max-width: 500px;
  margin: 2rem auto;
  padding: 2rem;
  border-radius: 0.5rem;
  background: #fff;
  text-align: center;
}

label {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
  color: #ffc107;
}

.input-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 1rem;
}

.input-box input {
  padding: 0.5rem;
  border: 2px solid #ffc107;
  border-radius: 0.3rem;
}

.control {
  margin-bottom: 1rem;
  color: #888;
  font-size: 0.8rem;
}

.gender-text {
  margin-bottom: 1rem;
  color: #ffc107;
}

.gender-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding-bottom: 1rem;
}

.gender-item {
  padding: 0.5rem 1rem;
  border: 2px solid #ffc107;
  border-radius: 0.3rem;
  margin-right: 0.5rem;
  background: none;
  color: #333;
  cursor: pointer;
}

.gender-item.selected {
  background: #ffc107;
}

.submit-btn {
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.3rem;
  background: #ffc107;
  color: #333;
  cursor: pointer;
}

.checkbox-container {
  margin: 1rem 0;
}

.checkbox-item {
  display: flex;
  color: #333;
}

.social-buttons {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}

.social-btn {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
  padding: 0.5rem 1rem;
  border: 2px solid #ffc107;
  border-radius: 0.5rem;
}

.social-text {
  margin-left: 1rem;
}

.icon {
  width: 2rem;
  height: 2rem;
}

.brand {
  color: #ffc107;
  font-weight: bold;
}

.login-text {
  color: #fff;
}

@media (min-width: 600px) {
  .social-buttons {
    flex-direction: row;
  }
}
